<template>
    <section class="section-counter">
        <div>
            <h2>Count from component</h2>
            Count: {{ count }}
            <button @click="decrementCount">-</button>
            <button v-on:click="incrementCount">+</button>
        </div>

        <div>
            <h2>Count from store</h2>
            Count: {{ this.$store.state.count }}
            <button v-on:click="decrementStoreCount">-</button>
            <button v-on:click="incrementStoreCount">+</button>
        </div>
    </section>
</template>

<script>
export default {
    name: 'CounterComponent',
    data() {
        return {
            count: 0,
        }
    },
    methods: {
        incrementCount() {
            console.log("incrementCount");
            this.count++;
        },
        decrementCount() {
            console.log("decrementCount");
            this.count--;
        },
        incrementStoreCount() {
            this.$store.commit('incrementCount')
        },
        decrementStoreCount() {
            this.$store.commit('decrementCount');
        },
    }
}
</script>